const ALLOWED_MEDIA_TYPES = ['image'];

function imageSizes(image) {
	if (typeof image === 'object') {
		let sizes = [];
		if (image.sizes && image.mime_type !== 'image/svg+xml') {
			for (let [name, size] of Object.entries(image.sizes)) {
				let url = new URL(size.url);
				url = url.pathname;
				sizes.push(`${url} ${size.width}w ${size.height}h`);
			}
		}
		if (sizes.length > 1) {
			return sizes.join(', ');
		}
	}
}

function imageSources(image) {
	if (typeof image === 'object') {
		let sizes = [];

		if (image.media_details.sizes && image.mime_type !== 'image/svg+xml') {
			for (let [name, size] of Object.entries(
				image.media_details.sizes
			)) {
				let url = `${size.source_url} ${size.width}w ${size.height}h`;
				sizes.push({
					dataSrcset: url,
					dataAspectratio: `${size.width}/${size.height}`,
					media: `--media-${name}`,
					type: size.mime_type,
				});
			}
		}
		if (sizes.length > 0) {
			return sizes.map((size) => (
				<source
					type={size.type}
					data-srcset={size.dataSrcset}
					media={size.media}
					data-aspectratio={size.dataAspectratio}
				/>
			));
		}
	}
}

function bgSizes(image) {}

export function displayImage(imageID, classes = '', isBG = false) {
	if (typeof imageID !== undefined) {
		let image = wp.data.select('core').getMedia(imageID);

		if (typeof image === 'object') {
			let coverSize = isBG ? 'cover' : 'contain';
			return (
				<figure
					className={classNames(
						'figure media-wrapper image-wrapper',
						classes,
						{
							'is-bg': isBG,
						}
					)}
				>
					<picture class="media-content">
						<img
							loading="lazy"
							className="media-src media-image lazyload"
							alt={image.alt_text}
							title={image.title.rendered}
							srcSet="data:image/gif;base64,R0lGODlhAQABAAAAACH5BAEKAAEALAAAAAABAAEAAAICTAEAOw=="
							width={image.media_details.width}
							height={image.media_details.height}
							data-src={image.source_url}
							data-srcset={imageSizes(image)}
							data-parent-fit={coverSize}
							//eslint-disable-next-line
							data-parent-container=".media-wrapper"
						/>
					</picture>
				</figure>
			);
		}
	}
}

export const RWPBGImage = ({
	label,
	attributeName,
	attributeValue,
	srcsetName,
	srcsetValue,
	urlName,
	urlValue,
	setAttributes,
	...props
}) => {
	let img = false;
	if (attributeValue !== 0) {
		img = wp.data.select('core').getMedia(attributeValue);
	}

	let instructions = (
		<p>{__('You need permission to upload media.', 'rwp')}</p>
	);

	const onUpdateImage = (media) => {
		let url = new URL(media.url);
		url = url.pathname;
		img = media;
		setAttributes({
			[attributeName]: media.id,
		});
	};

	const onRemoveImage = () => {
		img = null;
		setAttributes({
			[srcsetName]: '',
			[attributeName]: 0,
			[urlName]: '',
		});
	};

	let panelStyle = {
		flexDirection: 'column',
	};
	return (
		<div className="editor-post-featured-image">
			<MediaUploadCheck fallback={instructions}>
				<MediaUpload
					title={__('Background image', 'rwp')}
					onSelect={onUpdateImage}
					allowedTypes={ALLOWED_MEDIA_TYPES}
					value={attributeValue}
					render={({ open }) => (
						<div className="editor-post-featured-image__container">
							<Button
								className={
									!img
										? 'editor-post-featured-image__toggle'
										: 'editor-post-featured-image__preview'
								}
								onClick={open}
								isLarge
							>
								{!attributeValue &&
									__('Set background image', 'rwp')}
								{!!attributeValue && !img && <Spinner />}
								{!!attributeValue && img && (
									<ResponsiveWrapper
										naturalWidth={img.media_details.width}
										naturalHeight={img.media_details.height}
									>
										<img
											src={img.source_url}
											alt={__('Background image', 'rwp')}
										/>
									</ResponsiveWrapper>
								)}
							</Button>
						</div>
					)}
				/>
			</MediaUploadCheck>
			{!!attributeValue && img && (
				<MediaUploadCheck>
					<MediaUpload
						title={__('Background image', 'rwp')}
						onSelect={onUpdateImage}
						allowedTypes={ALLOWED_MEDIA_TYPES}
						value={attributeValue}
						render={({ open }) => (
							<Button onClick={open} isSecondary>
								{__('Replace background image', 'rwp')}
							</Button>
						)}
					/>
				</MediaUploadCheck>
			)}
			{!!attributeValue && (
				<MediaUploadCheck>
					<Button onClick={onRemoveImage} isLink isDestructive>
						{__('Remove background image', 'rwp')}
					</Button>
				</MediaUploadCheck>
			)}
		</div>
	);
};
