import { __ } from '@wordpress/i18n';
import TokenList from '@wordpress/token-list';
const metadata = require('./block.json');
const styles = metadata.styles;
import { PanelBody, PanelRow, TextControl } from '@wordpress/components';
import {
    compose,
    createHigherOrderComponent,
    withState,
} from '@wordpress/compose';

import {
    InnerBlocks,
    InspectorControls,
    BlockControls,
    useBlockProps,
    __experimentalUseInnerBlocksProps as useInnerBlocksProps,
    withColors,
} from '@wordpress/block-editor';

import { useSelect, getSelectedBlock, withSelect } from '@wordpress/data';

import './edit.scss';

import {
    classNames,
    uniqueClasses,
    updateClassesFromAtts,
    hasBackgroundClass,
} from '../global/helpers';
import { RWPBGImage } from '../global/images';

const BLOCK_TEMPLATE = [['rwp/container', {}, [['rwp/row', {}]]]];
const ALLOWED_BLOCKS = ['rwp/container'];

wp.hooks.addFilter(
    'blocks.getBlockDefaultClassName',
    'rwp/section',
    (className, blockName) => {
        if (blockName !== 'rwp/section') {
            return className;
        }

        className = classNames('rwp', 'section-wrapper');

        return className;
    }
);

function sectionEdit(props) {
    const {
        attributes,
        setAttributes,
        clientId,
        isSelected,
        hasInnerBlocks,
        blockType,
        defaultVariation,
        variations,
        name,
        backgroundColor,
        setBackgroundColor,
        type,
        bgImage,
    } = props;
    const { innerClasses, bgImageId, className } = attributes;

    const innerBlocksProps = useInnerBlocksProps(
        {
            className: classNames('section-inner', innerClasses),
        },
        {
            templateLock: false,
            allowedBlocks: ALLOWED_BLOCKS,
            template: BLOCK_TEMPLATE,
            renderAppender: hasInnerBlocks
                ? undefined
                : InnerBlocks.ButtonBlockAppender,
        }
    );

    let classes = classNames(className);
    classes = hasBackgroundClass(bgImageId, backgroundColor, className);

    let img = false;
    if (bgImage != undefined) {
        img = bgImage;
    } else if (bgImageId !== 0) {
        img = wp.data.select('core').getMedia(bgImageId);
    }

    const blockProps = useBlockProps({
        className: classes,
        style: {
            backgroundImage: img ? `url(${img.source_url})` : '',
        },
    });

    return (
        <div {...blockProps}>
            <InspectorControls>
                <PanelBody
                    title={__('Section Settings', 'rwp')}
                    initialOpen={true}
                >
                    <RWPBGImage
                        label={__('Background Image', 'rwp')}
                        attributeName="bgImageId"
                        attributeValue={bgImageId}
                        setAttributes={setAttributes}
                    />
                    <PanelRow>
                        <TextControl
                            label={__('Inner Classes', 'rwp')}
                            value={innerClasses}
                            onChange={(val) =>
                                setAttributes({ innerClasses: val })
                            }
                        />
                    </PanelRow>
                </PanelBody>
            </InspectorControls>
            <div {...innerBlocksProps} />
        </div>
    );
}

export default compose(
    withSelect((select, props) => {
        const { clientId, name, setAttributes } = props;
        const { getBlockOrder } =
            select('core/block-editor') || select('core/editor');
        const { getMedia } = select('core');
        const { bgImageId } = props.attributes;

        const hasInnerBlocks = useSelect(
            (select) => {
                const { getBlock } = select('core/block-editor');
                const block = getBlock(clientId);
                return !!(block && block.innerBlocks.length);
            },
            [clientId]
        );

        const {
            getBlockVariations,
            getBlockType,
            getDefaultBlockVariation,
        } = select('core/blocks');

        return {
            clientId: clientId,
            blockType: getBlockType(name),
            bgImage: bgImageId ? getMedia(bgImageId) : null,
            defaultVariation: getDefaultBlockVariation(name, 'block'),
            variations: getBlockVariations(name, 'block'),
            hasInnerBlocks: hasInnerBlocks,
        };
    }),
    withColors({
        backgroundColor: 'background-color',
    })
)(sectionEdit);
